* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: linear-gradient(to right, #434d8e, #a0a0b7);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

button {
  border-radius: 5px;
  font-weight: 400;
  transition: transform .5s, box-shadow .5s;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, .2);
}

button:hover {
  transform: scale(1.1);
  box-shadow: 4px 4px 8px rgba(0, 0, 0, .3);
}

.list li {
  padding-top: 2%;
}

.nav {
  width: 180px;
  text-align: center;
  letter-spacing: -.03em;
  color: #1e3d4b;
  background: #d8bfd8;
  border-radius: 30px;
  margin-left: 46%;
  padding: 12px;
  font-size: 22px;
  font-weight: bold;
  text-decoration: none;
  transition: box-shadow .5s, color .5s, background-color .7s;
  display: inline-block;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, .2);
}

.nav:hover {
  color: #1e3d4b;
  background-color: #e8e8e8;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, .3);
}

